/* II. Styles reset
--------------------------------------- */
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

[hidden] {
  display: none;
}

button,
input {
  border: none;
  outline: none;
  border-radius: 0;
}

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em;
}

i {
  display: inline-block;
}

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}

blockquote,
q {
  quotes: blockquote, q #8220 blockquote, q #8220;
}
blockquote:before, blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

ul, ol {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* III. Default styles
--------------------------------------- */
/* 1. Basic
------------------- */
html {
  font-family: "Raleway", "Helvetica", sans-serif;
  font-feature-settings: "pnum" on, "lnum" on;
  font-weight: 400;
  line-height: 1.2;
  font-size: 10px;
  color: #2a2b31;
}

h1, h2, h3, h4, h5, h6,
p {
  cursor: default;
}

.container, .container_xl, .container_lg, .container_md, .container_sm {
  margin: 0 auto;
  padding: 0 1.5rem;
}

.container {
  max-width: 123rem;
}
.container_xl {
  max-width: 114rem;
}
.container_lg {
  max-width: 96rem;
}
.container_md {
  max-width: 72rem;
}
.container_sm {
  max-width: 54rem;
}

.d-none {
  display: none;
}
.d-block {
  display: block;
}
.d-inline {
  display: inline;
}

.scroll-hidden {
  /*для скрытия полосы прокрутки*/
  padding-right: 17px;
  overflow: hidden;
}

.wrapper {
  margin: 0 auto;
  max-width: 1920px;
  overflow: hidden;
  position: relative;
}

/* 2. Typography
-------------------------*/
address {
  font-style: normal;
}

.text_primary-blue {
  color: #1C84E3;
}
.text_medium {
  font-weight: 600;
}
.text_semibold {
  font-weight: 600;
}
.text_bold {
  font-weight: 700;
}

/* 3. Links
-------------------------*/
a, a:visited {
  text-decoration: none;
  outline: none;
  color: #52596F;
  transition: color 0.3s ease-in-out;
}
a.text-link, a:visited.text-link {
  color: #52596F;
  text-decoration: underline;
}

a.text-link:hover, a.text-link:focus {
  text-decoration: none;
}
a.text-link:active {
  text-decoration: none;
}

/* 4. Images
-------------------------*/
img {
  display: block;
  max-width: 100%;
  height: auto;
}

/* 5. Buttons
-------------------------*/
button,
input[type=submit],
input[type=reset],
input[type=button],
.btn {
  font-family: "Raleway", "Helvetica", sans-serif;
  text-decoration: none;
  cursor: pointer;
  user-select: none;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  background: none;
  overflow: hidden;
}
.btn__text {
  margin-left: 1.2rem;
}
.btn_full-width, .btn_lg, .btn_md, .btn_sm {
  width: 100%;
}
.btn_lg {
  max-width: 20rem;
}
.btn_md {
  max-width: 15rem;
}
.btn_sm {
  max-width: 10rem;
}

button[type=submit] {
  display: inline-block;
}

/* 6. Forms 
-------------------------*/
input:not(input[type=checkbox]),
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
}

input:-ms-input-placeholder {
  color: rgba(115, 255, 0, 0.938);
}

input::-webkit-input-placeholder {
  color: rgba(255, 0, 0, 0.95);
}

input::-moz-placeholder {
  color: rgba(8, 94, 255, 0.918);
}

input::-ms-clear {
  display: none;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

select::-ms-expand {
  display: none;
}

